.govsa-card {
    height: 225px;

    &:hover {
        background-color: $green;
        cursor: pointer;
        color: $white;
    }

    &:active {
        border-color: $yellow;
        box-shadow: 0px 0 0 1px $yellow;
    }
}

.card-body {
    padding-bottom: 0.25rem;
    align-items: center;
}

.govsa-card__tag {
    float: right;
    text-align: center;
    p {
        background-color: $white;
        border-radius: 8px 0px 0px 8px;
        border-color: $green;
        color: $black;
        padding: 0.25rem;
        // @include font-size($font-size-sm * .875);
        // font-weight: $font-weight-bold;
    }

}

.govsa-card__footer {
    padding: 15px 0px 0px 10px;
    background-color: $green;
    border-radius: 0 0 6px 6px;
    height: 60px;
    color: $black;

    div>div>span {
        font-weight: bold;
        padding-left: 15px;
    }
}

.govsa-card__container {
    flex: 1 0 50%;
    margin: 0px 5px;
    padding: 0px 5px 0px 0px;
}

[dir="rtl"]  {
    .govsa-card__tag {
        float: left;
        p {
            border-radius: 0px 8px 8px 0px;
        }
    }
    .govsa-card__footer {
        padding: 15px 10px 0px 0px;
        div>div>span {
            padding-right: 15px;
        }
    }
    .govsa-card__container {
        padding: 0px 0px 0px 5px;
    }
   
}